.overlay {
    position: absolute;
    left: 0;
    top: 0;
    background-color: rgba(0, 0, 0, 0.1);
    width: 100%;
    height: 100%;
    z-index: 1;
    visibility: hidden;
    overflow: hidden;
    transition: transform 0.3s cubic-bezier(0.17, 0.67, 0.83, 0.67);
}

.overlayVisible {
    visibility: visible;

    .drawer {
        transform: translateX(0);
    }
} 

.drawer {
    display: flex;
    flex-direction: column;
    position: absolute;
    width: 420px;
    right: 0;
    height: 100%;
    background: #FFFFFF;
    box-shadow: -10px 4px 24px rgba(0, 0, 0, 0.1);
    padding: 30px;
    transform: translateX(100%);
    transition: transform 0.3s ease-in-out;

    .items {
        overflow: auto;
        margin-bottom: 40px;
    }
}